<template>
    <div class="container">
		<transition name="fade">
			<div class="modal" v-if="showModal" @click.self="toggleCalForm()">
				<div class="modal-content">
					<div class="modal-header">
						<h2>{{ isThai ? "ปริมาณแคลอรี่ที่ต้องการต่อวัน" : "Your recommended calories intake per day" }}</h2>
					</div>
					<div class="modal-body">
						<div class="form-group">
							<input type="number" step="0.01" min="0" name="weight" required="required" v-model="frmWeight">
							<label for="input" class="control-label">{{ isThai ? "น้ำหนัก (กิโลกรัม)" : "Weight (kg)" }}</label>
							<i class="bar"></i>
						</div>
						<div class="form-group">
							<input type="number" step="0.01" min="0" name="height" required="required" v-model="frmHeight">
							<label for="input" class="control-label">{{ isThai ? "ส่วนสูง (เซนติเมตร)" : "Height (cm)" }}</label>
							<i class="bar"></i>
						</div>
						<div class="form-group">
							<input type="number" min="0" name="age" required="required" v-model="frmAge">
							<label for="input" class="control-label">{{ isThai ? "อายุ" : "Age" }}</label>
							<i class="bar"></i>
						</div>

						<h3>{{ isThai ? "เพศ" : "Gender" }}</h3>
						<div class="form-radio">
							<div class="radio">
								<label>
									<input type="radio" name="gender" value="male" checked="checked"  v-model="frmGender">
									<i class="helper"></i>{{ isThai ? "ผู้ชาย" : "Male" }}
								</label>
							</div>
							<div class="radio">
								<label>
									<input type="radio" name="gender" value="female" v-model="frmGender">
									<i class="helper"></i>{{ isThai ? "ผู้หญิง" : "Female" }}
								</label>
							</div>
						</div>

						<h3>{{ isThai ? "ระดับการใช้พลังงาน" : "Energy Factory" }}</h3>
						<div class="form-radio">
							<div class="radio">
								<label>
									<input type="radio" name="energy" value="1.2" checked="checked" v-model="frmEnergy">
									<i class="helper"></i>{{ isThai ? "เคลื่อนไหวน้อย" : "Sedentary" }}
								</label>
							</div>
							<div class="radio">
								<label>
									<input type="radio" name="energy" value="1.3" v-model="frmEnergy">
									<i class="helper"></i>{{ isThai ? "เคลื่อนไหวปานกลาง" : "Moderately Active" }}
								</label>
							</div>
							<div class="radio">
								<label>
									<input type="radio" name="energy" value="1.4" v-model="frmEnergy">
									<i class="helper"></i>{{ isThai ? "เคลื่อนไหวมาก" : "Active" }}
								</label>
							</div>
						</div><br><br>

						<span v-if="needCalories"><b>{{ isThai ? "ปริมาณแคลอรี่ที่ต้องการต่อวัน" : "Your calories intake per day is" }}</b> {{ needCalories }} {{ isThai ? "แคลอรี่" : "Calories" }}</span>
					</div>
				</div>
			</div>
		</transition>
    </div>
</template>

<script>
    export default {
        name: "PopupCalcium",
        props: {
            lang: String,
            showModal: Boolean
        },
        data() {
			return {
				frmWeight: null,
				frmHeight: null,
				frmAge: null,
				frmGender: "male",
                frmEnergy: "1.2",
            }
        },
        computed: {
            isThai() {
                return this.lang == "th";
            },
			needCalories() {
				if(!this.frmWeight || !this.frmHeight || !this.frmAge || !this.frmGender || !this.frmEnergy 
					|| (this.frmGender != "male" && this.frmGender != "female") 
					|| (this.frmEnergy < 1.2 || this.frmEnergy > 1.4))
					return null

				var total = 0

				if(this.frmGender == "male")
					total = 66.5 + 13.8 * this.frmWeight + 5 * this.frmHeight - 6.8 * this.frmAge
				else
					total = 655.1 + 9.6 * this.frmWeight + 1.9 * this.frmHeight - 4.7 * this.frmAge
				
				return (total * this.frmEnergy).toFixed(2)
			}
        },
		watch: {
			frmWeight: function (newVal, oldVal) {
				if(newVal == oldVal)
					return null

				var str = newVal.toString()
				var idx = str.indexOf(".")
				if(idx == -1 || str.length <= idx + 3)
					this.frmWeight = newVal
				else
					this.frmWeight = oldVal
			},
			frmHeight: function (newVal, oldVal) {
				if(newVal == oldVal)
					return null

				var str = newVal.toString()
				var idx = str.indexOf(".")
				if(idx == -1 || str.length <= idx + 3)
					this.frmHeight = newVal
				else
					this.frmHeight = oldVal
			},
		},
        methods: {
            toggleCalForm() {
                this.$emit('toggleCalForm') 
            }
        }
    };
</script>

<style lang="scss" scoped>
	/* VARIABLE */
	$color-highlight: #03a678;
    .container {
        overflow: auto;
        height: 100%;
    }
    
	.form-radio,
	.form-group {
		position: relative;
		margin-right: 30px;
		margin-top: 30px;
		display: inline-block;
	}

	.form-radio {
		margin-top: 15px;
		margin-bottom: 0px;
	}

	.form-group {
		input {
			height: 1.9rem;
			display: block;
			background: none;
			padding: 0.125rem 0.125rem 0.0625rem;
			font-size: 1rem;
			border-width: 0;
			border-color: transparent;
			line-height: 1.9;
			width: 100%;
			color: transparent;
			-webkit-transition: all 0.28s ease;
			transition: all 0.28s ease;
			box-shadow: none;

			&:focus, &:valid {
				color: #333;
				outline: none;
			}
		}

		.control-label {
			position: absolute;
			top: 0.25rem;
			pointer-events: none;
			padding-left: 0.125rem;
			z-index: 1;
			color: #b3b3b3;
			font-size: 1rem;
			font-weight: normal;
			-webkit-transition: all 0.28s ease;
			transition: all 0.28s ease;
		}

		.bar {
			position: relative;
			border-bottom: 0.0625rem solid #999;
			display: block;

			&::before {
				content: "";
				height: 0.125rem;
				width: 0;
				left: 50%;
				bottom: -0.0625rem;
				position: absolute;
				background: $color-highlight;
				-webkit-transition: left 0.28s ease, width 0.28s ease;
				transition: left 0.28s ease, width 0.28s ease;
				z-index: 2;
			}
		}

		input:valid ~ .control-label , input:focus ~ .control-label {
			font-size: 0.8rem;
			color: gray;
			top: -1rem;
			left: 0;
			color: $color-highlight;
		}

		input:valid ~ .control-label , input:focus ~ .bar::before {
			width: 100%;
			left: 0;
		}		
	}

	.form-radio {
		label {
			position: relative;
			cursor: pointer;
			padding-left: 2rem;
			text-align: left;
			color: #333;
			display: block;
		}
		input {
			width: auto;
			opacity: 0.00000001;
			position: absolute;
			left: 0;
		}
	}

	.radio {
		margin-bottom: 10px;
		margin-left: 30px;
		display: inline-block;

		.helper {
			position: absolute;
			top: -0.25rem;
			left: -0.25rem;
			cursor: pointer;
			display: block;
			font-size: 1rem;
			-webkit-user-select: none;
			-moz-user-select: none;
			-ms-user-select: none;
			user-select: none;
			color: #999;

			&::before,
			&::after {
				content: "";
				position: absolute;
				left: 0;
				top: 0;
				margin: 0.25rem;
				width: 1rem;
				height: 1rem;
				-webkit-transition: -webkit-transform 0.28s ease;
				transition: -webkit-transform 0.28s ease;
				transition: transform 0.28s ease;
				transition: transform 0.28s ease, -webkit-transform 0.28s ease;
				border-radius: 50%;
				border: 0.125rem solid currentColor;
			}

			&::after {
				-webkit-transform: scale(0);
				transform: scale(0);
				background-color: $color-highlight;
				border-color: $color-highlight;
			}
		}

		label:hover .helper {
			color: $color-highlight;
		}

		input:checked ~ .helper::after {
			-webkit-transform: scale(0.5);
			transform: scale(0.5);
		}

		input:checked ~ .helper::before {
			color: $color-highlight;
		}
	}

	/* MODAL */
	.modal {
		position: fixed;
		z-index: 200;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		overflow: auto;
		background-color: rgba(0, 0, 0, 0.4);

		.modal-content {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			background-color: #fefefe;
			height: 85vh;
			max-height: 600px;
			width: 85vw;
			max-width: 725px;
			border-radius: 10px;
			padding-bottom: 10px;
			box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
				0 6px 20px 0 rgba(0, 0, 0, 0.19);

			.modal-header {
				border-top-left-radius: 10px;
				border-top-right-radius: 10px;
				padding: 10px 16px;
				background-color: $color-highlight;
				color: white;
			}

			.modal-body {
				padding: 10px 16px;
				overflow: auto;
				height: calc(100% - 56px);
			}
		}
	}

</style>
