<template>
	<div id="app-container">
		<div class="content-container">
			
			<decloration-food></decloration-food>
			<decloration-food></decloration-food>

			<div class="navbar">
				<div class="language-container">
					<span class="language" @click="chooseThaiLanguage()">TH</span>
					&nbsp;|&nbsp;
					<span class="language" @click="chooseEnglishLanguage()">EN</span>
				</div>
			</div>

			<div class="left-container">
				<nutrition-label @selectPopup="selectPopup" :selectedPopup="selectedPopup" :lang="language" :contents="contents"></nutrition-label>
				<div class="reference"><a href="#" @mouseover="selectPopup('reference')" v-if="contents">{{ contents['popupReference'].label[language] }}</a></div>
			</div>
			<div class="right-container" v-if="selectedPopup">
				<div :class="['description', selectedPopup != null ? 'full-width' : '']">
					<div class="description-container">

						<div class="close" @click="selectPopup(null)">X</div>
						
						<transition name="fade" mode="out-in">
							<keep-alive>
								<component v-bind:is="currentPopupComponent" :lang="language" :content="currentContent"></component>
							</keep-alive>
						</transition>
					</div>
				</div>
			</div>
		</div>

		<div class="form-btn-container" @click="toggleCalForm()">
			<img src="./assets/icon-cal.png">
		</div>

		<modal-calculator :showModal="showCalForm" :lang="language" @toggleCalForm="toggleCalForm"></modal-calculator>
	</div>
</template>

<script>
	// label
	import NutritionLabel from "./components/NutritionLabel.vue";

	// popup
	import PopupIntroduction from "./components/popup/Introduction.vue";
	import PopupServingsize from "./components/popup/Servingsize.vue";
	import PopupCalories from "./components/popup/Calories.vue";
	import PopupFootnotes from "./components/popup/Footnotes.vue";
	import PopupFat from "./components/popup/Fat.vue";
	import PopupSodium from "./components/popup/Sodium.vue";
	import PopupCarbohydrate from "./components/popup/Carbohydrate.vue";
	import PopupProtein from "./components/popup/Protein.vue";
	import PopupVitamin from "./components/popup/Vitamin.vue";
	import PopupCalciumiron from "./components/popup/Calciumiron.vue";
	import PopupReference from "./components/popup/Reference.vue";

	// modal
	import ModalCalculator from "./components/modal/Calculator.vue";

	// decloration
	import DeclorationFood from "./components/decloration/Food.vue";

	// lib
	import Axios from 'axios';

	export default {
		name: "app",
		components: {
			"nutrition-label": NutritionLabel,
			"popup-introduction": PopupIntroduction,
			"popup-servingsize": PopupServingsize,
			"popup-calories": PopupCalories,
			"popup-footnotes": PopupFootnotes,
			"popup-fat": PopupFat,
			"popup-sodium": PopupSodium,
			"popup-carbohydrate": PopupCarbohydrate,
			"popup-protein": PopupProtein,
			"popup-vitamin": PopupVitamin,
			"popup-calciumiron": PopupCalciumiron,
			"popup-reference": PopupReference,
			"modal-calculator": ModalCalculator,
			"decloration-food": DeclorationFood
		},
		data() {
			return {
				selectedPopup: null,
				language: "en",
				showCalForm: false,
				loading: true,
				contents: null
			};
		},
		watch: {
			selectedPopup: function (val) {
				window.ga('set', 'page', '/' + val)
				window.ga('send', 'pageview');
			}
		},
		computed: {
			currentPopupComponent() {
				return "popup-" + this.selectedPopup.toLowerCase();
			},
			currentContent() {
				if(this.contents)
					return this.contents['popup' + this.selectedPopup.charAt(0).toUpperCase() + this.selectedPopup.slice(1).toLowerCase()]
				else return null
			}
		},
		mounted() {
			Axios.get('./content.json').then(response => (this.contents = response.data))
		},
		created() {	
			this.$nextTick(() => {
				window.addEventListener("resize", this.adjustPopup);
				window.addEventListener("resize", this.adjustFooter);
				this.adjustPopup();
				this.adjustFooter(true);
			});

			this.loading = false;
		},
		methods: {
			selectPopup(index) {
				this.selectedPopup = index;
			},
			chooseThaiLanguage() {
				this.language = "th";
			},
			chooseEnglishLanguage() {
				this.language = "en";
			},
			toggleCalForm() {
				this.showCalForm = !this.showCalForm;
			},
			adjustPopup() {
				if(document.body.offsetWidth > 840 && this.selectedPopup == null) 
					this.selectedPopup = "Introduction"
			},
			adjustFooter(marginSet = false) {
                var icons = document.getElementsByClassName("food-container");
                var screenWidth = document.body.offsetWidth - 60;
                var lastLeft = -40;
                for (var i = 0; i < icons.length; i++) {
                    var currentLeft = Math.floor(
                        (screenWidth / (icons.length - 1)) * i
                    );
                    icons[i].style.bottom = "0px";
                    icons[i].style.left = currentLeft + "px";
                    if (marginSet === true)
                        icons[i].children[0].style.marginTop = (Math.random() * 20 + 20) + "px";

                    if (currentLeft - lastLeft < 40)
                        icons[i].style.display = "None";
                    else {
                        lastLeft = currentLeft;
                        icons[i].style.display = "block";
                    }
                }
            }
		}
	};
</script>

